@import '../styles/mixins.scss';

.figure {
    display: flex;
    flex-basis: calc(100%);
    margin: 8px 0;
    position: relative;
    transition: all 0.1s ease-in-out;
    overflow: hidden;
    cursor: pointer;
    animation: flash 0.4s ease infinite;
    
    @include md {
        flex-basis: calc(100% / 3 - 8px);
    }

    &:before {
        content: "";
        background: url('data:image/svg+xml;utf8;base64,');
        width: 64px;
        height: 64px;
        z-index: 10;
        cursor: pointer;
        user-select: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%) scale(0.85);
        transform-origin: 50%;
        opacity: 0;
    }

    &:after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        transition: all 0.1s ease-in-out;
        opacity: 0;
    }

    &:hover {
        @include md {
            &:before {
                opacity: 1;
                animation: zoom 0.2s cubic-bezier(.87,.13,.36,.43) forwards;
            }
            
            &:after {
                opacity: 1;
            }
        }

        .imgDiv {
            transform: scale(1.05);
        }
    }

}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.1s cubic-bezier(.67,1.38,.92,.93);
}

.imgDiv {
    transition: all 0.2s cubic-bezier(.17,.67,.6,1);
    background-size: cover;
    width: 100%;
    height: 100%;
    padding-bottom: 100%;
    background-position: 50%;
}

@keyframes zoom {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.7;
    }
}

@keyframes flash {
    0% {
        background-color: #ccc;
    }
    50% {
        background-color: #d4d4d4;
    }
    100% {
        background-color: #ccc;
    }
}